import React from "react";
import PropTypes from "prop-types";
import Helmet from "react-helmet";

import Header from "../components/Header";
import "./index.css";

import "../assets/fontawesome";
import "../assets/fontawesome-brands/faTwitter";

const TemplateWrapper = ({ children }) => (
  <div className="app">
    <Helmet
      title="Rafael Mello Campanari"
    >
      <link
        rel="stylesheet"
        type="text/css"
        href="https://fonts.googleapis.com/css?family=Cookie&amp;text=RafaelMelloCampanari"
      />
      <meta name="description" content="My personal website" />
      <meta name="keywords" content="software developer, front-end developer, javascript, react, flow, graphql, typescript, jest, mocha, cypress, css3" />
      <link rel="stylesheet" type="text/css" href="https://fonts.googleapis.com/css?family=Alegreya+Sans" />
      <script async src="https://use.fontawesome.com/releases/v5.0.6/js/brands.js" />

    </Helmet>

    {children()}
  </div>
);

TemplateWrapper.propTypes = {
  children: PropTypes.func
};

export default TemplateWrapper;



